.home {
    .site-content {
        @include general-width;
        .contnet-continaer {
            width: 75%;
        }
        #secondary {
            width: 25%;
            margin-top: 30px;
            padding-left: 36px;
        }
        .widget-left {
            width: 200px;
        }
    }
    .viewmore-btn {
        background: grey; // width: 630px;
        height: 32px;
        padding-top: 4px;
        margin-top: 30px;
        text-align: center;
        a {
            text-decoration: none;
            color: white;
            font-size: 12px;
            text-align: center;
            padding-top: 20px;
        }
    }
    .front-posts {
        display: flex;
        padding-bottom: 20px;
        .front-post-single {
            max-width: 165px;
            margin: 0 12px;
        }
        .front-post-single:nth-child(4) {
            margin-left: 12px;
            margin-right: 0;
        }
        .front-post-single:nth-child(1) {
            margin-left: 0;
            margin-right: 12px;
        }
    }
    .post-th-title {
        a {
            font-size: 14px;
        }
    }
    .front-post-contianer {
        margin: 50px 0;
        .content-title a {
            font-size: 15px;
        }
    }
    .best-thumb,
    .content-p-container {
        width: 50%;
    }
    .fpc-trtv,
    .fpc-news {
        border-bottom: 1px solid $color__border;
    }
    .fpc-trtv {
        h2 {
            display: flex;
            span {
                padding-left: 10px;
            }
        }
        .yellow-dot {
            width: 22px;
            height: 22px;
            border-radius: 20px;
            background: rgb(243, 223, 65);
            i {
                color: white;
                margin-left: 10px;
                margin-top: 2px;
                font-size: 15px;
            }
        }
    }
    .fp-post-header {
        p {
            color: $color__viewall;
        }
    }


//video posts
.trtv-tb-w-bg {
    position: relative;
}

.play-btn {
    @include video-playbtn-shape;
    top: 61px;
    left: 130px;
}

.shape-t {
    background: url("../..img/triangle.png") no-repeat center 4.5rem;
    background-size: contain, 11rem 26rem, cover;
}

  
    

//best posts 
.firstpage-content-th {
    display: flex;
    width: 100%;
}
.front-best-post-date{
    @include post-container-date;
}
.content-p-container {
padding-left: 10px;
.content-p{
   margin: 0;
}
    a{
        text-decoration: none;
        color: $color_orange;
        font-size: 14px;
    }
}
.best-single-container {
    @include post-container;
    a{
        text-decoration: none;
        color: black;
    }
    h3{
        margin-top: 0;
    }
}
.best-thumb {
    img{
    @include  post-container-thumb;
}
}
.fp-post-header{
display: flex;
justify-content: space-between;
}


}